body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body{
  background: black;
}
.app {
  text-align: center;
  color: white;
}

.board{
  margin-top: 10px;
}

.score{
  color: white;
  position: absolute;
  top:0;
  left: 80px;
  top: -30px;

}
.canvas{
  margin:auto;
  position: relative;
  margin: auto;
  width: 800px;
}
.paused{
  position: absolute;
  left:0px;
  right:0px;
  margin: auto;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  color: white;
  -webkit-text-stroke: 2px black;
}
.gameStart{
  color: white;
  position: absolute;
  top: 0px;
  right: 80px;
}
.gameStart button{
  font-size: 16px;
  height: 50px;
  width: 100px;
}
@media only screen and (max-device-width: 667px){
  .gameStart{
    position: absolute;
    left: 0px;
  }
  .gameStart button{
    font-size: 16px;
    height:40px;
    width:75px;
  }
  .canvas{
    position: relative;
    margin: auto;
    height:450px;
    background: blue;
    text-align: center;
  }
  .score{
    color: white;
    position: absolute;
    left: 0px;
    bottom:0px;
  }
  .score h1{
    font-size: 20px;
  }
}

